.HeaderInfo {
  margin-left: 3vw;
  position: relative;
  top: 13.5vw;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  max-width: 50%;
}

.HeaderInfo h1 {
  font-size: 2.5rem;
}

.HeaderInfo .overview {
  font-size: 1.5rem;
  font-weight: 400;
  text-shadow: 2px 2px 4px rgb(0 0 0 / 45%);
}

.HeaderButtons {
  margin-top: 3%;
  display: flex;
}

@media only screen and (max-width: 768px) {
  .HeaderInfo {
    display: none;
  }
}
